export default "@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,300&display=swap);\n.container {\n  max-width: 1170px;\n}\n@media screen and (max-width: 575px) {\n  .container {\n    padding: 0px 15px;\n    max-width: 100%;\n    width: 100%;\n  }\n}\n.bg-01 {\n  padding: 50px 0px;\n  background: #d9dee296;\n  position: relative;\n  overflow: hidden;\n}\n.bg-01 .se-box {\n  display: block;\n  margin: -15px 0px;\n  padding: 20px 25px;\n  position: relative;\n  text-align: center;\n}\n.bg-01 .se-box .icon {\n  padding: 0 0 10px 0px;\n  position: relative;\n  display: block;\n  height: 60px;\n  width: 60px;\n  background-color: #002147;\n  border-radius: 50px;\n  line-height: 60px;\n  text-align: center;\n  margin: 0px auto;\n  color: #fff;\n  font-size: 20px;\n  transition: 0.5s;\n}\n.bg-01 .se-box .icon::before {\n  position: absolute;\n  content: \"\";\n  width: 80px;\n  height: 80px;\n  border-radius: 50px;\n  left: -10px;\n  top: -10px;\n  border: 2px dashed #002147;\n}\n.bg-01 .se-box .icon:hover {\n  background: #4b4f5a;\n  cursor: pointer;\n}\n.bg-01 .se-box .content {\n  padding: 20px 0px;\n  position: relative;\n  display: inline-block;\n}\n.bg-01 .se-box .content h3 {\n  font-size: 20px;\n  color: #002147;\n  text-transform: capitalize;\n  margin: 10px 0 0 0;\n  cursor: pointer;\n}\n.bg-01 .se-box .content p {\n  color: #444444;\n  font-size: 15px;\n  line-height: 26px;\n  margin: 10px 0px 20px 0px;\n}\n/*# sourceMappingURL=data:application/json;base64, */"