export default ".cc {\n  background-color: whitesmoke;\n}\n\n.main {\n  width: 100%;\n  height: 400px;\n  background-image: url('a-propos-de-nous.png');\n  background-position: center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  opacity: 0.7;\n  padding-top: 50px;\n  padding-left: 50px;\n}\n\n.main-contain {\n  padding: 20px;\n  width: 300px;\n  border: 2px solid #0c2d50;\n  color: #0c2d50;\n}\n\n.main-contain h3 {\n  font-size: 50px;\n  letter-spacing: 25px;\n}\n\n.main-contain p {\n  letter-spacing: 4px;\n}\n\n.bottomy {\n  width: 25%;\n  display: flex;\n  flex-direction: row;\n  justify-content: space-between;\n}\n\n.bottomy-one, .bottomy-two {\n  width: 50%;\n  height: 3px;\n  background-color: #0c2d50;\n}\n\n.bottomy-center {\n  margin-left: 5%;\n  margin-right: 5%;\n  position: relative;\n  bottom: 15px;\n}\n\n.bottomy-center i {\n  font-size: 30px;\n}\n\n.main-title {\n  width: 100%;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin-top: 50px;\n  color: #0c2d50;\n}\n\n.main-title h3 {\n  text-transform: uppercase;\n  font-weight: bold;\n  font-size: 25px;\n  letter-spacing: 5px;\n}\n\n/*----------------------------------------*/\n\n/*  17.1 Teachers Area\n/*----------------------------------------*/\n\n.teachers-area {\n  padding: 0 0 50px;\n  margin-top: 40px;\n}\n\n.single-teacher-item {\n  box-shadow: 1px 0 5px rgba(0, 0, 0, 0.2);\n  position: relative;\n  transition: all 0.5s !important;\n  cursor: pointer;\n}\n\n.single-teacher-item:hover {\n  box-shadow: 0 6px 20px rgba(34, 30, 31, 0.3);\n}\n\n.single-teacher-image > a {\n  display: inline-block;\n  position: relative;\n}\n\n.single-teacher-image > a img, .single-teacher-image > a {\n  width: 100%;\n}\n\n.single-teacher-image > a:after {\n  content: \"\";\n  height: 100%;\n  left: 0;\n  opacity: 0;\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\n  filter: alpha(opacity=0);\n  position: absolute;\n  top: 0;\n  transition: all 0.3s ease 0s;\n  width: 100%;\n}\n\n.single-teacher-item:hover .single-teacher-image > a:after {\n  opacity: 1;\n  -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n  filter: alpha(opacity=100);\n}\n\n.single-teacher-text {\n  background: #f6f6f6 none repeat scroll 0 0;\n  border-bottom: 3px solid #0c2d50;\n  padding: 20px 26px 90px;\n  text-align: center;\n  transition: all 0.4s;\n}\n\n.single-teacher-item:hover .single-teacher-text {\n  margin-top: -45px;\n  padding: 20px 26px 66px;\n  position: absolute;\n  z-index: 9;\n}\n\n.single-teacher-text > h3 {\n  font-family: raleway;\n  font-weight: 600;\n  margin-bottom: 4px;\n  padding-top: 20px;\n  font-size: 24px;\n}\n\n.single-teacher-item:hover .single-teacher-text > h3 {\n  padding-top: 0;\n}\n\n.single-teacher-text > h3 a {\n  color: #222222;\n  transition: all 0.3s;\n}\n\n.single-teacher-text > h4 {\n  font-family: raleway;\n  font-weight: 500;\n}\n\n.single-teacher-text > p {\n  display: none;\n  padding-top: 13px;\n}\n\n.single-teacher-item:hover .single-teacher-text p {\n  display: block;\n}\n\n.social-links > a {\n  border: 1px solid #2d3e50;\n  border-radius: 50%;\n  color: #2d3e50;\n  display: inline-block;\n  font-size: 14px;\n  height: 27px;\n  line-height: 25px;\n  margin: 0 3px;\n  text-align: center;\n  width: 27px;\n}\n\n.social-links > a:hover {\n  color: #ffffff;\n}\n\n.social-links {\n  bottom: 38px;\n  left: 0;\n  margin: auto;\n  position: absolute;\n  right: 0;\n  text-align: center;\n  width: 100%;\n  transition: all 0.3s ease 0s;\n}\n\n.single-teacher-item:hover .social-links {\n  bottom: 25px;\n}\n\n.single-course-details {\n  background: #f6f6f6 none repeat scroll 0 0;\n  border-bottom: 3px solid #0c2d50;\n  margin-bottom: 80px;\n  padding: 33px 0;\n}\n\n.single-course-details .single-item-text {\n  padding-right: 48px;\n}\n\n.course-details-area a img {\n  margin-left: -35px;\n}\n\n.single-course-details a {\n  display: block;\n}\n\n.single-course-details .col-md-6:first-child {\n  padding-right: 3px;\n}\n\n.details {\n  margin-left: 10%;\n  margin-right: 10%;\n  height: 500px;\n  overflow-x: hidden;\n  overflow-y: scroll;\n  padding-right: 20px;\n}\n\n.details::-webkit-scrollbar {\n  width: 3px;\n  background: white;\n}\n\n.details h3 {\n  margin-top: 20px;\n  margin-bottom: 20px;\n  white-space: nowrap;\n  width: 30%;\n  padding-bottom: 5px;\n  border-bottom: 3px solid #0c2d50;\n}\n\n.details h3 i {\n  margin-right: 2px;\n  /* width: 17px; */\n  font-size: 30px;\n  position: relative;\n  bottom: 3px;\n}\n\n.details div, .details p {\n  text-align: justify;\n}\n\n.space {\n  margin-bottom: 80px;\n}\n\n.mimi {\n  height: -15px;\n}\n\n.contain-img {\n  width: 100%;\n  height: 260px;\n  margin-top: 30px;\n}\n\n.contain-img img {\n  width: 100%;\n  height: 100%;\n}\n\n.overlay {\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  background-color: #33333391;\n  overflow: hidden;\n  width: 0;\n  height: 90%;\n  transition: 0.5s ease;\n}\n\n.contain-img:hover .overlay {\n  width: 100%;\n}\n\n.text {\n  color: white;\n  font-size: 20px;\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  white-space: nowrap;\n}\n\n@media screen and (max-width: 480px) {\n  .details h3 {\n    margin-top: 20px;\n    margin-bottom: 20px;\n    white-space: normal;\n    width: 100%;\n    padding-bottom: 5px;\n    border-bottom: 3px solid #0c2d50;\n  }\n}\n\n@media screen and (max-width: 400px) {\n  .main {\n    padding-left: 0px;\n  }\n}\n\n.contain-text {\n  margin-top: 5px;\n  display: flex;\n  flex-direction: row;\n  justify-content: flex-end;\n  opacity: 0.7;\n}\n/*# sourceMappingURL=data:application/json;base64, */"